/**
 * Ëé∑ÂèñÊâÄÊúâÈ¶ôÊ∞õ‰∫ßÂìÅÂàÜÁ±ª
 * @returns {Array} ÂàÜÁ±ªÂàóË°®
 */
export function getAromaCategories() {
  return aromaCategories
}

/**
 * Ëé∑ÂèñÈ¶ôÊ∞õ‰∫ßÂìÅÂàóË°®
 * @param {string} categoryId - ÂèØÈÄâÁöÑÂàÜÁ±ªID
 * @returns {Array} ‰∫ßÂìÅÂàóË°®
 */
export function getAromaProducts(categoryId) {
  if (!categoryId || categoryId === '') {
    return sampleAromaProducts
  }
  return sampleAromaProducts.filter(product => product.categoryId === categoryId)
}

/**
 * Ëé∑ÂèñÂçï‰∏™È¶ôÊ∞õ‰∫ßÂìÅËØ¶ÊÉÖ
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {Object|null} ‰∫ßÂìÅÂØπË±°Êàñnull
 */
export function getAromaProductById(productId) {
  if (!productId) {
    return null
  }
  return sampleAromaProducts.find(product => product.id === productId) || null
}

/**
 * Ëé∑ÂèñÂ§ñÈÉ®ÂïÜÂüéÈìæÊé•
 * @param {string} productId - ‰∫ßÂìÅID
 * @returns {string|null} ÂïÜÂüéÈìæÊé•Êàñnull
 */
export function getAromaShopUrl(productId) {
  const product = getAromaProductById(productId)
  if (!product) {
    return null
  }
  return product.shopUrl || null
}

export default {
  aromaCategories,
  sampleAromaProducts,
  getAromaCategories,
  getAromaProducts,
  getAromaProductById,
  getAromaShopUrl
}
